body {
  background-color: #092537;
}

.header {
  width: 100%;
  height: 100px;
  margin: 1% 40% 1% 40%;
}

.logo img {
  width: 60px;
  height: 60px;
  float: left;
}

.name p {
  float: left;
  color: white;
  font-size: 40px;
  margin: 10px 0px 10px 10px;
  font-weight: 800;
}

.phone {
  float: left;
  width: 50%;
  height: auto;
  margin-bottom: 3%;
}

.phone img {
  width: 250px;
  height: 515px;
  float: left;
  margin: 0px 0px 0px 50%;
}

.content {
  color: white;
  float: left;
  width: 50%;
  height: auto;
  margin-bottom: 2%;
  padding-top: 5%;
}

.content img {
  width: 200px;
  height: 93px;
}

.content h1 {
  font-size: 40px;
}
